<template>
  <div id="main-page">
    <img
      alt="Settings"
      class="btn"
      @click="openScreen('settings')"
      style="left: 75px; top: 45px; width: 50px; height: 50px"
      src="./assets/settings_icon.jpeg"
    />
    <button
      class="btn"
      @click="newSolve"
      style="left: 170px; top: 20px; width: 200px; height: 100px; border-radius: 50px; font-size: 20px"
    >New Solve</button>
    <twisty-player
      disabled
      ref="cube"
      :alg="cube_state"
      id="cube"
      background="none"
      :control-panel="settings.playback_controls ? 'bottom-row' : 'none'"
      :hint-facelets="settings.hint_stickers ? 'floating' : 'none'"
      style="left: 40%; top: 5%; width: 500px; height: 600px"
    ></twisty-player>
    <answer-button
      v-for="(answer, key) in answers"
      :key="key"
      :index="key"
      :answer="answer"
      :move_options="move_options"
      @answered="doMove(key)"
    ></answer-button>
    <p
      ref="question"
      style="left: 5%; top: 20%; font-size: 40px; width: 30%; text-align: center; color: white"
    ></p>
    <p style="left: 55%; top: 80%; font-size: 30px; color: white">{{ previous_correct }}</p>
    <p
      style="left: 55%; top: 90%; font-size: 20px; color: white"
    >Moves left: {{ moves_list.length }}</p>
    <div id="blur" v-if="current_screen != 'main'"></div>
  </div>
  <settings-screen
    v-if="current_screen == 'settings'"
    :settings="settings"
    @exit="openScreen('main')"
    @update-settings="updateSettings($event)"
  />
  <!-- <script type="module" src="https://esm.sh/cubejs@1.3.2"></script> -->
</template>

<script>
// importing components, trivia database and external libraries
import AnswerButton from "./components/AnswerButton.vue";
import SettingsScreen from "./components/SettingsScreen.vue";
import trivia from "./assets/trivia.json";
import * as Cube from "cubejs";
import * as twisty from "cubing/twisty";
twisty;
export default {
  data() {
    return {
      cube_state: " ",
      answers: this.answers,
      previous_correct: "",
      moves_list: [],
      current_screen: "main",
      settings: {
        hint_stickers: true,
        playback_controls: false,
        move_animations: true,
        trivia: {
          general: true,
          science: true,
          entertainment: true,
          other: true
        }
      }
    };
  },
  methods: {
    updateSettings: function(settings) {
      this.settings = settings;
    },
    openScreen: function(screen) {
      this.current_screen = screen;
    },
    // reset cube and questions
    newSolve: function() {
      this.cube_state = " ";
      this.questions = this.shuffleArray(trivia["results"]);
      Cube.initSolver();
      var scramble = Cube.scramble();
      this.cube_state = scramble;
      this.$refs.cube.buttons.buttons["play-step-backwards"].click();
      setTimeout(() => {
        this.$refs.cube.play();
        this.$refs.cube.jumpToEnd();
      }, 0);
      this.cube = new Cube();
      this.cube.move(scramble);
      this.moves_list = this.cube.solve().split(" ");
      this.updateQuestion();
    },
    // returns true if question is selected in filters, otherwise false
    filterQuestion: function(category) {
      if (
        this.settings.trivia ==
        { general: false, science: false, entertainment: false, other: false }
      ) {
        return true;
      }
      if (category == "General Knowledge") {
        return this.settings.trivia.general;
      } else if (category.includes("Science")) {
        return this.settings.trivia.science;
      } else if (category.includes("Entertainment")) {
        return this.settings.trivia.entertainment;
      } else {
        return this.settings.other;
      }
    },
    // from: https://www.geeksforgeeks.org/how-to-shuffle-an-array-using-javascript/
    shuffleArray: function(array) {
      for (var i = array.length - 1; i > 0; i--) {
        // Generate random number
        var j = Math.floor(Math.random() * (i + 1));

        var temp = array[i];
        array[i] = array[j];
        array[j] = temp;
      }
      return array;
    },
    reverseMove: function(move) {
      if (move.length == 1) {
        return move + "'";
      } else if (move[1] == "2") {
        return move;
      } else {
        return move[0];
      }
    },
    // runs when user selects an answer
    doMove: function(id) {
      // parameter is id of selected answer (0-3)
      this.cube_state += " " + this.move_options[id];
      this.cube.move(this.move_options[id]);
      this.$refs.cube.buttons.buttons["play-step-backwards"].click();
      setTimeout(() => {
        this.$refs.cube.play();
        if (!this.settings.move_animations) {
          this.$refs.cube.jumpToEnd();
        }
      }, 0);
      if (id == this.correct_answer) {
        this.previous_correct = "Correct!";
        this.moves_list.shift();
      } else {
        this.previous_correct = "Incorrect.";
        var new_solution = this.cube.solve().split(" ");
        if (new_solution.length > this.moves_list.length) {
          this.moves_list.unshift(this.reverseMove(this.move_options[id]));
        } else {
          this.moves_list = new_solution;
        }
      }
      this.questions.shift();
      this.updateQuestion();
    },
    // shows next question and answers
    updateQuestion: function() {
      if (this.moves_list.length == 0) {
        this.$refs.question.innerHTML = "Congratulations! You solved the cube";
      } else {
        while (
          !this.filterQuestion(this.questions[this.questions_index]["category"])
        ) {
          this.questions_index += 1;
          if (this.questions_index == this.questions.length) {
            this.questions_index = 0;
          }
        }
        this.current_question = this.questions[this.questions_index];
        this.$refs.question.innerHTML = this.current_question["question"];
        this.answers = this.current_question["incorrect_answers"];
        this.answers.push(this.current_question["correct_answer"]);
        this.answers = this.shuffleArray(this.answers);
        this.correct_answer = this.answers.indexOf(
          this.current_question["correct_answer"]
        );
        this.setAnswers();
      }
    },
    // pairs each answer with a move on the cube
    setAnswers: function() {
      this.move_options = [];
      var next_move = this.moves_list[0];
      var suffix;
      if (next_move.length == 1) {
        suffix = "";
      } else {
        suffix = next_move[1];
      }
      var cube_layers = ["R", "U", "F", "L", "D", "B"];
      cube_layers.splice(cube_layers.indexOf(next_move[0]), 1);
      for (var i = 0; i < 4; i++) {
        if (i == this.correct_answer) {
          this.move_options.push(next_move);
        } else {
          let index = Math.floor(Math.random() * cube_layers.length);
          this.move_options.push(cube_layers[index] + suffix);
          cube_layers.splice(index, 1);
        }
      }
    }
  },
  // initialisation when page loads
  mounted() {
    this.questions = this.shuffleArray(trivia["results"]);
    this.questions_index = 0;
    this.newSolve();
  },
  components: {
    AnswerButton,
    SettingsScreen
  }
};
</script>

